.message {
    padding-inline: 70px;
}

.imageAndText {
    flex-direction: row-reverse;
    display: flex;
    gap: 10px;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {

    position: relative;

    background: #0066CC;
    padding-left: 12px;
    padding-right: 20px;
    border-radius: 6px;
    padding-block: 12px;
    position: relative;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
}

.text:after {
    content: '';
    display: block;
    position: absolute;
    width: 20px;
    background-color: white;
    top: 0;
    bottom: -2px;
    right: -10px;

    border-bottom-left-radius: 20px;
}

.name {
    font-weight: 600;
    font-size: 16px;
    color: white;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
    color: white;
}

.time {
    flex-direction: row-reverse;
    display: flex;
    text-align: center;
    font-weight: 600;
    font-size: 10px;
    color: #000;
    padding-right: 11px;
    padding-bottom: 25px;
}