.friendMessage {
    padding-inline: 70px;
    display: flex;

}

.friendImageAndText {
    display: flex;
    align-items: center;
    gap: 10px;

}

.friendImageAndText img {
    width: 48px;
    height: 48px;

}

.friendText {
    background: #F5F7FB;
    padding-left: 35px;
    padding-right: 5px;
    border-radius: 6px;
    padding-block: 12px;
    position: relative;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
}

.friendText:after {
    content: '';
    display: block;
    position: absolute;
    width: 20px;
    background-color: white;
    top: 0;
    bottom: -2px;
    left: -10px;
    border-top-left-radius: 6px;
    border-bottom-right-radius: 20px;
}

.friendName {
    font-weight: 600;
    font-size: 16px;
    color: #000;
}

.friendMessageText {
    font-weight: 400;
    font-size: 16px;
    color: #000;

}

.friendTime {

    font-weight: 600;
    font-size: 10px;
    color: #000;
    /* padding-right: 11px; */
}